export const initialState = {
        isSuccess: false,
};

//To Store the Actions
const global = (state = initialState, action) => {
        switch (action.type) {
                case "SUCCESS":
                        return {
                                ...state,
                                isSuccess: true,
                        };
                case "FAIL":
                        return {
                                ...state,
                                isSuccess: false,
                        };
                default:
                        return state;
        }
};
export default global;
